import { actions } from "../action/searchAction";

const initialState = {
  networkStatus: { status: -1, reson: "" },
  searchword: [], // word
  type: 0, // type
  filter: 0, // 
  searchresult: {
    index: 0, //     
    page: 0, //    page 
    pages: 0, //  (    )
    cocktails: [] //    
  },
  recommend: {
    tags: [], //MainPage  
    result: [] //  
  }
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actions.SEARCH.SUCCESS: {
      const { data } = action.payload;
      if (data.recommend) {
        return {
          ...state,
          recommend: {
            ...state.recommend,
            result: data.cocktailArray
          },
          networkStatus: { status: 200, reson: "" }
        };
      } else {
        return {
          ...state,
          searchword: data.word,
          type: data.type,
          filter: data.filter,
          searchresult: {
            ...state.searchresult,
            cocktails: data.cocktailArray,
            page: data.page,
            pages: data.pages
          },
          networkStatus: { status: 200, reson: "" }
        };
      }
    }
    case actions.RECOMMEND.SUCCESS: {
      const { data } = action.payload;
      return {
        ...state,
        recommend: {
          ...state.recommend,
          tags: data
        },
        networkStatus: { status: 200, reson: "" }
      };
    }
    case actions.RECOMMEND.FAILED: {
      const { data } = action.payload;
      return {
        ...state,
        networkStatus: { status: data.status, reson: data.reson }
      };
    }
    default:
      return state;
  }
};

export default reducer;
