import { actions } from "../action/recipeAction";
const initialState = {
  state: "fail"
};
//      api  
//       state    
//, ID   1 ,    ->      1    
//enrolmentReducer !

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actions.ALL.SUCCESS: {
      const { result } = action.payload;
      return {
        bAllRecipeResult: result
      };
    }
    case actions.BYID.SUCCESS: {
      const { result } = action.payload;

      const stuffs = result[0].ingredient;
      const photos = result[0].image;
      // const comments = [
      //   {nick: " A", comments: " 1", time: "00:00"},
      //   {nick: " B", comments: " 2", time: "11:11"},
      //   {nick: " C", comments: " 3", time: "22:22"},
      //   {nick: " D", comments: " 4", time: "12:44"}
      // ];
      const comments = result[0].comment;
      const recipe_info = {
        cocktail: result[0].name,
        description: result[0].description,
        nick: result[0].owner, 
        like: result[0].scrap, 
        comment: 0,
        view: result[0].view,
        tags: result[0].tag,
        alcohol: result[0].numIngredient,
        glass: result[0].glass
      };

      return {
        ...state,
        state: "success",
        stuffs: stuffs,
        photos: photos,
        comment: comments,
        recipe_info: recipe_info,
      };
    }
    case actions.TAGBYLATEST.SUCCESS: {
      const { result } = action.payload;
      return {
        bIDCheckResult: result
      };
    }
    case actions.TAGBYVIEW.SUCCESS: {
      const { result } = action.payload;
      return {
        bIDCheckResult: result
      };
    }
    case actions.STUFFBYLATEST.SUCCESS: {
      const { result } = action.payload;
      return {
        bIDCheckResult: result
      };
    }
    case actions.STUFFBYVIEW.SUCCESS: {
      const { result } = action.payload;
      return {
        bIDCheckResult: result
      };
    }

    case actions.ADDCOMMENT.SUCCESS: {
      const { result } = action.payload;

      if (result.ok === 1) {
        return {
          ...state,
          state: "success",
        };
      } else return {...state};
      
    }
    default:
      return state;
  }
};

export default reducer;
