import React from "react";
import classNames from "classnames/bind";
import styles from "./ShakeList.scss";
import ShakeItem from "./ShakeItem";

const cx = classNames.bind(styles);

/**
 * @author AnGwangHo
 * @description Shake Me Overay Layout
 * @param id Component 
 * @param className css  default : shakelist
 * @param shakeitem  array  [{color, name, ml, %, size}, ...]
 * @param direction    (-> : 0, -> : 1)
 * @param startposition      (margin )
 * @param endposition       
 * @param width  List  .
 * @param height  List  .
 */
const ShakeList = ({
  id,
  className = cx("shakelist"),
  shakeitem,
  direction = 0,
  startposition,
  endposition,
  width,
  left
}) => {
  const listStyle = {
    width: width,
    left: left
  };
  const containerStyle = {
    height: endposition,
    bottom: startposition
  };

  return (
    <div id={id} className={className} style={listStyle}>
      <div className={cx("shakelist_container")} style={containerStyle}>
        {shakeitem.map(item => {
          return (
            <ShakeItem
              key={Math.random()}
              color={item.color}
              name={item.name}
              ml={item.ml}
              per={item.ratio}
            />
          );
        })}
      </div>
    </div>
  );
};

export default ShakeList;
