import React from "react";
import classNames from "classnames/bind";
import styles from "./Popup.scss";
import Div from "../Div/Div";

const cx = classNames.bind(styles);

/**
 * @author AnGwangHo
 * @description  Div
 * @param id Block Div ID
 * @param className Popup style(default : innercontainer)
 * @param content Popup  Tags
 * @param onClick Block Div Onclick 
 */
const Popup = ({ id, className = "innercontainer", content, onClick }) => {
  return (
    <div id={id} className={styles.popup} onClick={onClick}>
      <Div
        id={id + "_innercontainer"}
        className={cx(className)}
        content={content}
      />
    </div>
  );
};

export default Popup;
